<script>
// import BlankBG from "@/components/BlankBG.vue"

import $ from 'jquery'

import router from '@/router';
// import { useStore } from "vuex";
import store from '@/store';
// import { ref } from 'vue';

export default {
  name: "ArticePage",
  components: {
    // BlankBG,
  },
  data() {
    return {
      text: "",
      titles: "",
      article: "",
      author: "匿名",
      tittle: "",

      authorId: "",
      articleId: "",

      labels: [],
      comments: [],
      articleHot: [],

      comment: "",
      screenWidth: 1024,

    };
  },

  watch: {
    $route: {
      immediate: true,
      handler() {
        // if (this.$route.query.authorId != this.authorId || this.$route.query.articleId != this.articleId) {//需要监听的参数
        this.articleId = 70
        this.authorId = 13
        this.show_labels(this.articleId)
        this.show_article(this.articleId)
        this.get_hot_list()
        this.get_comment_list()
        this.loadAuthorInfo(this.authorId)
        // }
      }
    },
  },
  mounted() {
    this.screenWidth = window.innerWidth;
    console.log("屏幕宽度：" + this.screenWidth);
  },
  methods: {

    // 时间戳：1637244864707
    /* 时间戳转换为时间 */
    timestampToTime(timestamp) {
      timestamp = timestamp ? timestamp : null;
      let date = new Date(timestamp);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
      let Y = date.getFullYear() + '-';
      let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
      let D = (date.getDate() < 10 ? '0' + date.getDate() : date.getDate()) + ' ';
      let h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
      let m = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
      let s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
      return Y + M + D + h + m + s;
    },

    loadAuthorInfo(authorId) {
      const that = this
      if (authorId == store.state.user.id) {
        that.author = store.state.user.username;
        return;
      } else if (authorId == store.state.friend.id) {
        that.author = store.state.friend.username
        return;
      }

      store.dispatch("getFriendInfo", {
        friendId: authorId,
        success() {
          that.author = store.state.friend.username
        },
        error() {
          that.author = "作者匿名"
        }

      })



    },

    del_comment(commentId) {
      const that = this
      $.ajax({
        url: store.state.BaseUrl + "/api/comment/" + commentId,
        type: "delete",
        headers: {
          Authorization: "Bearer " + store.state.user.token,
        },
        success(resp) {
          if (resp.code == 1) {
            console.log(resp)
            that.get_comment_list()

          } else {
            console.log(resp)
          }
        }

      })
    },


    send_comment() {
      const that = this

      that.comment = that.comment.trim()
      if (that.comment.length <= 2) {
        alert("评论不能少于两个字符")
        return
      } else if (that.comment.length >= 100) {
        alert("评论不能长于一百个字符")
        return
      }
      $.ajax({
        url: store.state.BaseUrl + "/api/comment/",
        type: "post",
        headers: {
          Authorization: "Bearer " + store.state.user.token,
        },
        data: {
          comment: that.comment,
          answerArticleId: that.articleId,
          authorId: store.state.user.id

        },
        success(resp) {
          if (resp.code == 1) {
            console.log(resp)
            that.comment = ""
            that.get_comment_list()

          } else {
            console.log(resp)
          }
        }

      })
    },

    get_comment_list() {
      const that = this
      $.ajax({
        url: store.state.BaseUrl + "/api/comment/" + that.articleId,
        type: "get",
        headers: {
          Authorization: "Bearer " + store.state.user.token,
        },
        success(resp) {
          if (resp.code == 1) {
            console.log(resp)
            that.comments = resp.content
          } else {
            console.log(resp)
          }
        },
        error(resp) {
          console.log(resp)
        }

      })
    },

    goto_article(articleId, authorId) {

      if (this.articleId == articleId) {
        // console.log("当前文章展示")
        return
      }

      router.push({
        path: "/article",
        query: {
          authorId: authorId,
          articleId: articleId
        }
      })
    },

    handleAnchorClick(anchor) {
      const { preview } = this.$refs;
      const { lineIndex } = anchor;

      const heading = preview.$el.querySelector(`[data-v-md-line="${lineIndex}"]`);

      if (heading) {
        preview.scrollToTarget({
          target: heading,
          scrollContainer: window,
          top: 60,
        });
      }
    },

    show_labels(articleId) {
      // const store = useStore()
      const that = this
      $.ajax({
        url: store.state.BaseUrl + "/api/label/" + articleId,
        type: "get",
        headers: {
          Authorization: "Bearer " + store.state.user.token,
        },
        success(resp) {
          if (resp.code == 1) {
            that.labels = resp.content;
            // console.log("获取标签成功:" + that.labels)
          } else {
            console.log(resp.content)
          }
        }, error() {
          console.log("获取标签失败")
        }

      })
    },
    show_article(articleId) {
      const that = this
      $.ajax({
        url: store.state.BaseUrl + "/api/article/common/" + articleId,
        type: "GET",
        // headers: {
        //     Authorization: "Bearer " + store.state.user.token
        // },
        success(resp) {

          if (resp.code == 1) {

            that.text = resp.content.content
            that.article = resp.content
            that.authorId = resp.content.authorId
            that.tittle = resp.content.tittle
            console.log(resp)
            setTimeout(that.test, 1000);
          } else {
            console.log("获取文章失败")
          }


        },
        error(resp) {
          console.log(resp)
        }

      })
    },

    get_hot_list() {
      const that = this
      // const store = useStore()
      $.ajax({
        //热门文章
        url: store.state.BaseUrl + "/api/article/list/hot/" + 1 + "/" + that.authorId,
        type: "get",
        headers: {
          Authorization: "Bearer " + store.state.user.token,
        },

        success(resp) {

          that.articleHot = resp.content.articleList;
          // console.log(that.articleHot)


        },

      })

    },
    test() {
      const anchors = this.$refs.preview.$el.querySelectorAll('h1,h2,h3,h4,h5,h6');
      const titles = Array.from(anchors).filter((title) => !!title.innerText.trim());

      if (!titles.length) {
        this.titles = [];
        return;
      }

      const hTags = Array.from(new Set(titles.map((title) => title.tagName))).sort();

      this.titles = titles.map((el) => ({
        title: el.innerText,
        lineIndex: el.getAttribute('data-v-md-line'),
        indent: hTags.indexOf(el.tagName),
      }));
    }
  },

  setup() {



  }

}

</script>


