a {
  color: #08272b;
  /*字体颜色*/
  font-size: small;
  font-weight: 1000;
  text-decoration: none;
  /*去除a标签下划线*/
  /* outline: none; */
  /*去掉a标签默认轮廓*/
}

.hr-solid-content {
  /* color: #a2a9b6; */
  color: black;
  border: 0;
  font-size: 12px;
  padding: 1em 0;
  position: relative;
}

.hr-solid-content::before {
  content: attr(data-content);
  position: absolute;
  padding: 0 1ch;
  line-height: 1px;
  border: solid #d0d0d5;
  border-width: 0 99vw;
  width: fit-content;
  /* for IE浏览器 */
  white-space: nowrap;
  left: 50%;
  transform: translateX(-50%);
}

.hr-solid-content::after {
  content: attr(data-content);
  position: absolute;
  padding: 4px 1ch;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: transparent;
  border: 1px solid #d0d0d5;
}

.hr-wavy {
  border: 0;
  padding: 3px;
  background: repeating-linear-gradient(135deg, black 0px, #0358f7 1px, transparent 1px, transparent 6px);
}
</style>